.adminlist-card {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.5);
    background-color: #4B0082; /* dark purple background */
    color: #fff; /* white text color */
    margin-bottom: 10px;
    padding: 10px; /* add padding for more visual appeal */
  }
  
  .adminlist-card h2 {
    font-size: 1.5rem; /* increase font size for admin name */
    margin-right: 10px;
  }
  
  .adminlist-card button {
    background-color: #fff; /* white button background */
    color: #4B0082; /* dark purple button text color */
    border: 2px solid #fff; /* white button border */
    border-radius: 5px;
    padding: 5px 10px; /* add some padding for more visual appeal */
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    margin-bottom: 10px; /* add some spacing between buttons */
  }
  
  .adminlist-card button:hover {
    background-color: #fff;
    color: #fff; /* change button text color on hover */
    border-color: #4B0082; /* change border color on hover */
    box-shadow: 0px 0px 5px #4B0082; /* add shadow on hover for more visual appeal */
  }
  
